import { mapActions } from 'vuex'

export default {
  name: 'v-main-page',
  methods: {
    ...mapActions([
      'SET_NUMBER_METHOD'
    ]),
    goToMethod (data) {
      switch (data) {
        case 0:
          this.$router.push('/first')
          this.SET_NUMBER_METHOD(0)
          break
        case 1:
          this.$router.push('/second')
          this.SET_NUMBER_METHOD(1)
          break
        case 2:
          this.$router.push('/third')
          this.SET_NUMBER_METHOD(2)
          break
        case 3:
          this.$router.push('/fourth')
          this.SET_NUMBER_METHOD(3)
          break
        default:
          this.SET_NUMBER_METHOD(0)
          this.$router.push('/first')
      }
    }
  },
  data: () => ({
    startIntro: true
  })
}
</script>
